.appl1_sgpt_cnt_main_footer_inputbox {
    padding: 10px 15px;
    border: 1px solid #d8e4e9;
    border-radius: 15px;
    position: relative;
    width: 100%;
}

.appl1_sgpt_cnt_main_footer_inputbox_textarea {
    color: #000;
    width: 96%;
    height: 50px;
    margin-bottom: 20px;
    border: none;
    resize: none;
    background: transparent;
}

.appl1_sgpt_cnt_main_footer_inputbox_textarea::-webkit-scrollbar-button {
    background-repeat: no-repeat;
    width: 0;
    height: 0;
}

.appl1_sgpt_cnt_main_footer_inputbox_textarea::-webkit-scrollbar-track {
    background-color: transparent;
    opacity: 0.5;
}

.appl1_sgpt_cnt_main_footer_inputbox_textarea::-webkit-scrollbar-thumb {
    border-radius: 50px;
    background-color: #592cff;
    opacity: 0.5;
}

.appl1_sgpt_cnt_main_footer_inputbox_textarea::-webkit-scrollbar-thumb:hover {
    background-color: #592cff;
}

.appl1_sgpt_cnt_main_footer_inputbox_textarea::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

.appl1_sgpt_cnt_main_footer_inputbox_textarea:focus-visible {
    outline: none;
    border: none;
}

.appl1_sgpt_cnt_main_footer_inputbox_counter {
    display: flex;
    font-size: 10px;
    color: rgba(0, 0, 0, 0.63);
}

.appl1_sgpt_cnt_main_footer_inputbox_counter_first,
.appl1_sgpt_cnt_main_footer_inputbox_counter_second {
    line-height: 1 !important;
}

.appl1_sgpt_cnt_main_footer_inputbox_post {
    width: 40px;
    height: 40px;
    position: absolute;
    right: -1px;
    bottom: -1px;
    border-top-left-radius: 15px;
    border-bottom-right-radius: 15px;
    cursor: pointer;
    background-color: #592cff;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml,%3Csvg version='1.2' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' width='24' height='24'%3E%3Ctitle%3E1564528_fly_messager_send_communication_email_icon-svg%3C/title%3E%3Cstyle%3E .s0 %7B fill: %23fff %7D %3C/style%3E%3Cg id='info'%3E%3C/g%3E%3Cg id='icons'%3E%3Cpath id='send' class='s0' d='m21.5 11.1l-17.9-9c-0.9-0.4-1.9 0.4-1.5 1.3l2.5 6.7 11.4 1.9-11.4 1.9-2.5 6.7c-0.3 0.9 0.6 1.7 1.5 1.2l17.9-9c0.7-0.3 0.7-1.3 0-1.7z'/%3E%3C/g%3E%3C/svg%3E");
}

.appl1_sgpt_cnt_dialog_loader {
    position: absolute;
    display: flex;
    right: 0;
    background-color: #ffffffab;
    border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
        width: 100%;
        bottom: 0;
        height: 100%;
    z-index: 9999;
    align-items: center;
    justify-content: center;
}